import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/accounting/';

const state = {
  dashboard: { balance: '806.1000', todayTx: { debitamount: '826.0900', creditamount: '19.9880' } },
  list_data: [{}],
  current_page: 1,
  type_transactions: 'deposit',
  detailTrx: false,
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_LIST(state, _source) {
    state.list_data = _source;
  },
  SET_BOARD(state, _source) {
    state.dashboard = _source;
  },
  SET_PAGE(state, _source) {
    state.current_page = _source;
  },
  SET_TYPE(state, _source) {
    state.type_transactions = _source;
  },
  SET_DETAIL(state, _source) {
    state.detailTrx = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getDashboard({ commit }, _type) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}dashboard`, { headers });
      commit('SET_BOARD', response.data.data);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response);
      return error.response;
    }
  },
  async getList({ commit }, _type) {
    commit('SET_TYPE', _type);
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}list?page=1&per_page=10`, { headers });
      commit('SET_LIST', response.data.data.list_data ? response.data.data.list_data : []);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response);
      return error.response;
    }
  },
  async getPrevData({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };

      const response = await axios.get(`${BASE_URL}list?page=${state.current_page + 1}&per_page=10`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      commit('SET_PAGE', state.current_page + 1);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getNextData({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };

      const response = await axios.get(`${BASE_URL}list?page=${state.current_page + 1}&per_page=10`, { headers });
      commit('SET_LIST', response.data.data.list_data);
      commit('SET_PAGE', state.current_page + 1);
      return response.data;
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  resetState({ commit }) {
    const state_settings = {
      error: false,
      success: false,
      error_message: '',
      success_message: '',
    };
    commit('SET_STATS', state_settings);
  },
};
const getters = {
  boardData: (state) => state.dashboard,
  listData: (state) => state.list_data,
  stateData: (state) => state.state_settings,
  pageData: (state) => state.current_page,
  typeData: (state) => state.type_transactions,
  detailTrx: (state) => state.detailTrx,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
