import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://api.specta.app/v1/private/admin/other_settings/';

const state = {
  data_settings: [
    {
      address: '',
      type: '',
      bnb_balance: 0,
      usdt_balance: 0,
    },
  ],
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
};

const mutations = {
  SET_SETTINGS(state, _source) {
    state.data_settings = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getSettings({ commit }) {
    console.log('running');
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}admin_wallet`, { headers });
      console.log(response.data.data);
      commit('SET_SETTINGS', response.data.data);
      return response;
    } catch (error) {
      console.log('Error:', error);
      return error.response.data;
    }
  },
};
const getters = {
  settingData: (state) => state.data_settings,
  stateData: (state) => state.state_settings,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
